.App {
  text-align: center;
}

.App-heart {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-heart {
    animation: App-heart-size infinite 1s linear;
  }
}

.App-header {
  background-color: #ffabcf;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-heart-size {
  0%    {scale: 1;}
  50%   {scale: 0.95;}
  100%  {scale: 1;}
}
